

.container{
	width:95%;
}

.card{
	
	padding: 15px;
	margin: 17px;
	background-color: rgba(121, 134, 203, 0.3);
	float: left;
	-webkit-box-shadow: 0px 0px 48px -1px rgba(121,121,133,1);
	box-shadow: 0px 0px 48px -1px rgba(121,121,133,1);
	width: 95%;
	

}

.content{

	margin:10px;
	font-size: 18px;
	font-weight: solid;
	color: black;
	float: left;
	position: relative;
	font-family: "Times New Roman", Times, serif;
	
}

.bio{
	float: right;
	width: 74%;
	word-wrap: auto;
	font-family: "Times New Roman", Times, serif;
	font-size: 15px;
	color: black;
	position: relative;
	right: 15px;
	padding: 3px;
}

.image{
	border-style: solid;
	border: 5px;
	width: 200px;
	height: 200px;
	-webkit-box-shadow: 17px 9px 20px 0px rgba(0,0,0,0.75);
	box-shadow: 17px 9px 20px 0px rgba(0,0,0,0.75);

}


